
export const saveParams = {
  //刷新后保留params 到session中，但是params中的函数无法保存,同样传递无法JSON.stringify的对象也是无法保存的
  beforeCreate(){
    if(!sessionSave) return;
    const JSONParams = sessionStorage.getItem(this.$route.name+'__params');
    if(JSONParams){
      const params = JSON.parse(JSONParams);
      for(let key in params){
        if(params.hasOwnProperty(key)){
          this.$route.params[key] = params[key]
        }
      }
      sessionStorage.removeItem(this.$route.name+'__params')
    }
    if(Object.keys(this.$route.params).length !== 0){
      window.addEventListener('beforeunload',()=>{
        const params = {};
        for(let key in this.$route.params){
          if(this.$route.params.hasOwnProperty(key) && typeof this.$route.params[key] !== 'function'){
            params[key] = this.$route.params[key]
          }
        }
        try{
          sessionStorage.setItem(this.$route.name+'__params',JSON.stringify(params))
        }catch(e){}
      })
    }
  },
} 

export default {
  beforeRouteLeave (to, from, next) {
    const nextRouteParams = this.$vnode.componentOptions.Ctor.extendOptions.nextRouteParams;
    if(nextRouteParams && nextRouteParams.apply(this)[to.name]){
      const params = nextRouteParams.apply(this)[to.name];
      for(let key in params){
        if(params.hasOwnProperty(key)){
          if(typeof params[key] === 'function'){
            to.params[key] = (...arg) => params[key].apply(this,arg)
          }else{
            to.params[key] = params[key]
          }
        }
      }
    }
    next()
  },
  //是否需要全局刷新保存params数据
  // mixins:[saveParams]  
}