<template>
  <div id="app">
    <transition :name="transitionName"  @after-enter="afterEnter">
      <keep-alive :include="cachedViews">
        <router-view class="router"/>
      </keep-alive>
    </transition>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cachedViews: [],
      transitionName: ''
    }
  },
  watch:{
    $route:{
      handler(to,from){
        //路由缓存
        const routes = this.$router.options.routes;
        const cachedViews = []
        if(!to.name)return;
        this.cachedViews.forEach(item=>{
          const it = routes.filter(route=>route.name === item)[0];
          if(it.meta.level < to.meta.level){
            cachedViews.push(item);
          }
        });
        cachedViews.push(to.name);
        this.cachedViews = cachedViews;
        
        //路由动画
        if (to.meta.level > from.meta.level) {
          this.transitionName = 'slide-left'
        } else if(to.meta.level < from.meta.level) {
          this.transitionName = 'slide-right'
        }else{
          this.transitionName = ''
        }
        const userAgent = navigator.userAgent;
        const index = userAgent.indexOf("Android")
        if (index >= 0) {
          const androidVersion = parseFloat(userAgent.slice(index + 8));
          console.log(androidVersion,'安卓版本')
          if (androidVersion < 7) {
            this.transitionName = ''
          }
        }
      },
      immediate:true
    }
  },
  methods: {
    afterEnter() {
      this.$bus.$emit('afterRouteEnter')
    },
  },
}
</script>
<style lang="less">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: @text-color;
  background: @background-color;
  min-height: 100vh;
  font-size: 30/@rem;
}
.router{
  position: absolute;
  overflow-y: scroll;
  left:0;bottom:0;top:0;right:0;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.slide-right-enter-active,.slide-right-leave-active,.slide-left-enter-active{
  transition: transform .3s ;
  -webkit-transition: -webkit-transform .3s ;
  will-change: transform;
}
.slide-left-leave-active{
  transition: transform .3s .1s;
  will-change: transform;
  -webkit-transition: -webkit-transform .3s .1s;
}
.slide-left-enter,.slide-right-leave-active{
  z-index: 1000000;
  //box-shadow: rgba(0,0,0,.3) pxToRem(-10) 0 pxToRem(5);
}
.slide-left-enter-to,.slide-left-leave-to{
  z-index: inherit;
}

.slide-right-enter{
  transform: translate(-50%,0);
}
.slide-right-enter-to{
  transform: translate(0,0);
}
.slide-right-leave{
  transform: translate(0,0);
}
.slide-right-leave-to{
  transform: translate(100%,0);
}


.slide-left-enter{
  transform: translate(100%,0);
}
.slide-left-enter-to{
  transform: translate(0,0);
}
.slide-left-leave{
  transform: translate(0,0);
}
.slide-left-leave-to{
  transform: translate(-50%,0);
}

</style>
