import Vue from 'vue'

export default {
  created(){
    if(!this.$vnode)return;
    const afterRouteEnter = this.$vnode.componentOptions.Ctor.extendOptions.afterRouteEnter;
    if(afterRouteEnter){
      this.$bus && this.$bus.$once('afterRouteEnter',()=>{
        afterRouteEnter.apply(this);
      })
    }
  }
}

